import React, { memo } from 'react';
import { IDs } from './Index';
import Svg from './SVG';
import { v4 as uuidv4 } from 'uuid';
import { AppState } from '../../reducers';
import { connect } from 'react-redux';
import { SvgParentChild } from '../../actions/constants';

interface SvgGenerator {
  ids: IDs;
  refs: SvgParentChild[];
}

function SvgGenerator({ ids, ...props }: SvgGenerator) {
  const generateSvg = () => {
    //Connect Center Pieces
    const svgs = [];
    let centerPieces = [];
    for (const ref of props.refs) {
      if (ref[2]) {
        centerPieces.push(ref[0]);
      }
    }

    for (let i = 0; i < centerPieces.length - 1; i++) {
      let parentRect = centerPieces[i].getBoundingClientRect();
      let childRect = centerPieces[i + 1].getBoundingClientRect();
      if (parentRect.y !== 0) {
        svgs.push(
          <Svg
            key={uuidv4()}
            parentRect={parentRect}
            childRect={childRect}
            center
          />
        );
      }
    }

    for (const ref of props.refs) {
      for (const id of ref[1]) {
        for (const childRef of props.refs) {
          if (childRef[0].id === id) {
            const parentRect = ref[0].getBoundingClientRect();
            const childRect = childRef[0].getBoundingClientRect();
            if (parentRect.y === 0 || childRect.y === 0) continue;
            svgs.push(
              <Svg
                key={uuidv4()}
                parentRect={parentRect}
                childRect={childRect}
              />
            );
          }
        }
      }
    }

    if (svgs.length) {
      return svgs;
    }
    return null;
  };

  return <div className='svgs'>{generateSvg()}</div>;
}

interface StateProps {
  refs: SvgParentChild[];
}

const mapStateToProps = (state: AppState): StateProps => ({
  refs: state.SvgRefs,
});

export default memo(connect(mapStateToProps)(SvgGenerator));
