import { SET_SVG_REFS, AppActions, SvgParentChild } from '../actions/constants';

export const SvgRefs = (
  state: SvgParentChild[] = [],
  action: AppActions
): SvgParentChild[] => {
  switch (action.type) {
    case SET_SVG_REFS:
      return [...state, action.SvgRef];
    default:
      return state;
  }
};
